<template>
    <div v-show="visible" class="some-div">
        <transition-group name="list-complete" class="photos" tag="div">
            <div class="photo" v-for="(img, index) in images" :key="img" @click="shuff">
                <img :src="img" class="photo__img" alt="instagram image">
            </div>
        </transition-group>
    </div>
</template>

<script>
    import shuffle from 'lodash.shuffle'

    export default {
        props: {imageSrc: {type: Array, required: true}},
        data() {
            return {
                images: this.imageSrc,
                visible: true
            }
        },
        methods: {
            shuff() {
                this.images = shuffle(this.images);
            }
        }
    }
</script>

<style scoped lang="scss">
    .photos {
        background-color: white;
        padding: 3rem 3rem 2rem 3rem;
        column-count: 3;
        column-gap: .5rem;
    }

    .photo {
        transition: all .5s;
        position: relative;
        display: inline-block;
        width: 100%;
        &__img {
            display: block;
            width: 100%;
        }
    }
</style>