<template>
    <div class="tags">
        <div class="tag" @click.once="step1 = 'New'">
            <span class="tag__text">
                {{step1}}
            </span>
        </div>
        <div class="tag" @click.once="step2 = 'Kojima\'s'">
            <span class="tag__text">
                {{step2}}
            </span>
        </div>
        <div class="tag" @click.once="step3 = 'game'">
            <span class="tag__text">
                {{step3}}
            </span>
        </div>
        <div class="tag" @click.once="step4 = 'that'">
            <span class="tag__text">
                {{step4}}
            </span>
        </div>
        <div class="tag" @click.once="step5 = 'we are'">
            <span class="tag__text">
                {{step5}}
            </span>
        </div>
        <div class="tag" @click.once="step6 = 'all'">
            <span class="tag__text">
                {{step6}}
            </span>
        </div>
        <div class="tag" @click.once="step7 = 'waiting for'">
            <span class="tag__text">
                {{step7}}
            </span>
        </div>
        <div class="tag">
            <span class="tag__text">
                <span v-if="step7 === 'management'">{{step8}}</span>
                <span v-else="step7 === 'waiting for'"><input v-model="secretInput" type="text"></span>
            </span>
        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                step1: 'construction',
                step2: 'engineering',
                step3: 'architecture',
                step4: 'structural design',
                step5: 'civil engineer',
                step6: 'industrial',
                step7: 'management',
                step8: 'mechanical',
                secretInput: ''
            }
        },
        methods: {
            KojimaInit() {
                this.$emit('kojima');
            }
        },
        watch: {
            secretInput: function (val) {
                if (val.toLowerCase() === 'death stranding') {
                    this.KojimaInit();
                    this.step1 = 'construction';
                    this.step2 = 'engineering';
                    this.step3 = 'architecture';
                    this.step4 = 'structural design';
                    this.step5 = 'civil engineer';
                    this.step6 = 'industrial';
                    this.step7 = 'management';
                    this.step8 = 'mechanical';
                    this.secretInput = '';
                }
            }
        }
    }
</script>

<style scoped lang="scss">
    .tags {
        padding: 3rem 0 2rem 2rem;
        background-color: white;
        display: flex;
        flex-wrap: wrap;
    }

    .tag {
        cursor: pointer;
        &:not(last-child) {
            margin-right: .6rem;
            margin-bottom: 1rem;
        }
        &__text {
            display: inline-block;
            padding: .3rem 1.5rem;
            border: 1px solid #eee;
            border-radius: 2px;
            transition: all .3s ease-out;
            color: #9c9c9c;
            font-size: 1.3rem;
            font-weight: 400;
            line-height: 2.4rem;
            &:hover,
            &:active {
                border: 1px solid #333333;
                background-color: #333333;
                color: white;
            }
        }
    }
</style>